<template>
  <div>
    <h1>食譜</h1>
    <div class="table-responsive">
      <table class="table">
        <thead class="thead-dark">
          <tr>
            <th>菜式名稱</th>
            <th>菜式名稱2</th>
            <th>豆知識</th>
            <th>豆知識2</th>
            <th>食材份量</th>
            <th>食材</th>
            <th>製作方法</th>
            <th>菜式</th>
            <th>類別</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="recipe in recipes" :key="recipe.id">
            <td>{{ recipe.菜式名稱 }}</td>
            <td>{{ recipe.菜式名稱2 }}</td>
            <td>{{ recipe.豆知識 }}</td>
            <td>{{ recipe.豆知識2 }}</td>
            <td>{{ recipe.食材份量 }}</td>
            <td>{{ recipe.食材 }}</td>
            <td>{{ recipe.製作方法 }}</td>
            <td>{{ recipe.菜式 }}</td>
            <td>{{ recipe.類別 }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
import txt from 'raw-loader!../recipe_data.csv'
import Recipe from '@/model/recipe';

export default {
  components: {
    
  },
  props: {

  },
  data() {
    return {
      recipes: [],
    }
  },
  computed: {

  },
  created() {
    console.log(txt);
    let rows = txt.split('\n');
    rows.forEach((row) => {
      console.log(row);
      let cols = row.split(',');
      let recipe = new Recipe();
      if (cols[0] == '菜式名稱') return;
      recipe.菜式名稱 = cols[0];
      recipe.菜式名稱2 = cols[1];
      recipe.豆知識 = cols[2];
      recipe.豆知識2 = cols[3];
      recipe.食材份量 = cols[4];
      recipe.食材 = cols[5];
      recipe.製作方法 = cols[6];
      recipe.菜式 = cols[7];
      recipe.類別 = cols[8];
      this.recipes.push(recipe);
    });
  },
  mounted() {

  },
  methods: {
    
  },
}
</script>
